<template>
    <li
        @click.stop="toyDetails(toy._id)"
        class="toy-preview flex column"
        @mouseenter="isHovering = true"
        @mouseleave="isHovering = false"
    >
        <div class="img-container ratio-square">
            <img
                src="https://c0.klipartz.com/pngpicture/590/818/gratis-png-lego-duplo-lego-10615-duplo-mi-primer-tractor-ertl-toy-company-toy.png"
                alt=""
            />
        </div>
        <h4>{{ toy.name }}</h4>
        <h5>{{ toy.type }}</h5>
        <h3>{{ toy.price }}$</h3>
        <h4 class="inStock" v-if="toy.inStock">In stock</h4>
        <h4 class="outOfStock" v-else>Out of stock</h4>
        <transition name="fade">
            <span v-show="isHovering" class="toy-btns">
                <button @click.stop="editToy">
                    <i class="fas fa-edit fa-icon"></i>
                </button>
                <button @click.stop="emitDelete(toy._id)">
                    <i class="fas fa-trash-alt fa-icon"></i>
                </button>
                <!-- <button @click.stop="toyDetails(toy._id)">Details</button> -->
            </span>
        </transition>
        <star-rating
            class="star-rating"
            :increment="0.5"
            :max-rating="5"
            inactive-color="white"
            active-color="gold"
            :star-size="15"
            :border-width="1"
            v-model="rating"
            border-color="#999"
        >
        </star-rating>
    </li>
</template>


<script>
import StarRating from "vue-star-rating";

export default {
    name: "toy-preview",
    props: {
        toy: Object,
    },
    data() {
        return {
            isHovering: false,
            rating: 0,
        };
    },
    methods: {
        emitDelete(toyId) {
            this.$emit("deleteToy", toyId);
        },
        toyDetails(toyId) {
            this.$router.push(`/details/${toyId}`);
        },
        editToy() {
            this.$router.push(`/edit/${this.toy._id}`);
        },

        setRating: function (rating) {
            this.rating = rating;
        },
        getRandomIntInclusive(min, max) {
            min = Math.ceil(min);
            max = Math.floor(max);
            return Math.floor(Math.random() * (max - min + 1) + min); //The maximum is inclusive and the minimum is inclusive
        },
    },
    components: {
        StarRating,
    },
    created() {
        this.rating = this.getRandomIntInclusive(1,5)
        
    },
};
</script>
