 <template>
    <section class="admin-page">
        <div class="chart-container">
            <chart
                v-if="toys"
                :color="color"
                :data="chartData"
                :labels="chartLabels"
            />
        </div>
        <div class="map-container flex column align-center">
              <div class="map-btns">
                <el-button @click="centerMap(0)" type="primary"
                    >Tel Aviv</el-button
                >
                <el-button @click="centerMap(1)" type="primary"
                    >Netanya</el-button
                >
                <el-button @click="centerMap(2)" type="primary"
                    >Holon</el-button
                >
            </div>
            <div class="gMap-container">
                <GmapMap
                    :center="mapProps.center"
                    :zoom="mapProps.zoom"
                    map-type-id="terrain"
                    style="width: 595px; height: 495px"
                >
                    <GmapMarker
                        :key="index"
                        v-for="(m, index) in markers"
                        :position="m.position"
                        :clickable="true"
                        :draggable="true"
                        @click="center = m.position"
                    />
                </GmapMap>
            </div>
        </div>
    </section>
</template>

  <script>
import chart from "../components/chart";
export default {
    components: {
        chart,
    },
    data() {
        return {
            color: "rgba(131, 216, 231, 0.644)",
            markers: [
                { position: { lat: 32.0853, lng: 34.781769 } },
                { position: { lat: 32.329369, lng: 34.856541 } },
                { position: { lat: 32.011261, lng: 34.774811 } },
            ],
            mapProps: { center: { lat: 32.171312, lng: 34.812193 }, zoom: 9 },
        };
    },
    computed: {
        toys() {
            return this.$store.getters.toys;
        },
        chartData() {
            const typeCount = this.toys.reduce((acc, toy) => {
                acc[toy.type] = acc[toy.type] ? acc[toy.type] + 1 : 1;
                return acc;
            }, {});
            const typePrice = this.toys.reduce((acc, toy) => {
                acc[toy.type] = acc[toy.type]
                    ? acc[toy.type] + toy.price
                    : toy.price;
                return acc;
            }, {});
            var typeAvgPrice = [];
            for (const type in typePrice) {
                typeAvgPrice.push(
                    (typePrice[type] / typeCount[type]).toFixed(2)
                );
            }
            return typeAvgPrice;
        },
        chartLabels() {
            var labels = [];
            this.toys.forEach((toy) => {
                if (!labels.includes(toy.type)) labels.push(toy.type);
            });
            return labels;
        },
    },
    methods: {
        centerMap(num) {
            this.mapProps.zoom = 13;
            this.mapProps.center = this.markers[num].position;
        },
    },
};
</script>








