<template>
    <section v-if="toy" class="toy-edit pages">
        <form
            class="flex column justify-center align-center"
        >
            <h2 class="edit-title">{{ title }}</h2>

            <el-upload
                :on-change="uploadImg"
                class="upload-demo"
                drag
                action="https://jsonplaceholder.typicode.com/posts/"
                :on-preview="handlePreview"
                :on-remove="handleRemove"
                :file-list="fileList"
                show-file-list
            >
                >
                <i class="el-icon-upload"></i>
                <div class="el-upload__text">
                    Drop file here or <em>click to upload</em>
                </div>
                <div class="el-upload__tip" slot="tip">
                    jpg/png files with a size less than 500kb
                </div>
            </el-upload>
            <h3>Name</h3>
            <ValidationProvider name="toy.name" rules="required">
                <div class="flex column align-center" slot-scope="{ errors }">
                    <input v-model="toy.name" />
                    <p class="error-msg">{{ errors[0] }}</p>
                </div>
            </ValidationProvider>

            <h3>Type</h3>
            <select class="action" v-model="toy.type">
                <option value="Funny">Funny</option>
                <option value="Adult">Adult</option>
                <option value="Educational">Educational</option>
            </select>

            <h3>Price</h3>
            <ValidationProvider name="toy.price" rules="required">
                <div class="flex column align-center" slot-scope="{ errors }">
                    <input v-model="toy.price" />
                    <p class="error-msg">{{ errors[0] }}</p>
                </div>
            </ValidationProvider>
            <label class="checkbox action"
                >In stock?
                <input type="checkbox" v-model="toy.inStock" />
            </label>
            <div class="edit-btns">
                <el-button type="button" @click="goBack">Back</el-button>
                <el-button @click.prevent="saveToy">Save</el-button>
            </div>
        </form>
    </section>
</template>
<script>
import { toyService } from "../services/toy.service.js";

import { extend } from "vee-validate";
import { required } from "vee-validate/dist/rules";
import { ValidationProvider } from "vee-validate";

extend("required", {
    ...required,
    message: "This field is required",
});

export default {
    data() {
        return {
            isEditing: false,
            toy: null,
            fileList: [],
        };
    },
    computed: {
        title() {
            return this.isEditing ? `Edit toy ${this.toy._id}` : "Add a toy";
        },
    },
    methods: {
        goBack() {
            this.$router.push("/");
        },
        saveToy() {
            console.log("toy in saveToy:", this.toy);
            let type = this.isEditing ? "editToy" : "addToy";
            this.$store.dispatch({
                type,
                toy: this.toy,
            });
            this.goBack();
        },
        uploadImg(file, fileList) {
            console.log("ev:", file, fileList);
        },
        handleRemove(file, fileList) {
            console.log(file, fileList);
        },
        handlePreview(file) {
            console.log(file);
        },
    },
    created() {
        if (!this.$route.params.id) {
            this.toy = toyService.getEmpty();
            this.isEditing = false;
        } else {
            console.log("id from params:", this.$route.params.id);
            const toyId = this.$route.params.id;
            toyService.getById(toyId).then((toy) => {
                console.log("toy:", toy);
                this.isEditing = true;
                this.toy = toy;
            });
        }
        console.log("toy in edit created:", this.toy);
    },
    components: {
        ValidationProvider,
    },
};
</script>

<style>
.error-msg {
    color: red;
}
</style>